@use 'sass:map';
@use '../config/_config' as *;

.section-modal {
    &__modal-content {
        color: #ffffff;
        background-color: $colorBgCard;
    }

    &__content {
        font-size: 12px;
        color: map.get($colorPrimary, "regular");

        &--description {
            @extend .section-modal__content;
            color: map.get($colorText, "light");
        }
    }
}