@use '../config/_config' as *;
@use 'sass:map';

@forward '../config/_config';

.section-we-do {
    .section-card--we-do {
        &:hover {
            cursor: pointer;
        }
    }

    .card-body {
        background-color: $colorBgCard;
    }

    &__image {
        @include imgToFillSpace;
    }

    &__description {
        color: map.get($colorText, "light");
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        @media(min-width: 768px) and (max-width: 991px) {
            font-size: 12px;
        }
    }

    &__lightcase {
        color: #ffffff;

        &:hover {
            color: #ffffff;
            text-decoration: none;
        }
    }
}